import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import { inputData } from '../../features/nicknameSlice';
import { useDispatch } from 'react-redux';
import { Positioner } from '../../components/Wrapper/styled'
import Button from '../../components/Button'
import * as Styled from './styled';

const Ready = () => {
    const imgUrl = "https://img.icons8.com/color/48/000000/box-important--v1.png"
    const [value, setValue] = useState('');
    const [login, setLogin] = useState(false);
    const dispatch = useDispatch();
    let navigate = useNavigate();

    const loginClick = (e) => {
        if(value.trim().length === 0)
		{
			alert(" .");
			setValue("");
			return;
        }
        
        setLogin(!login);
        dispatch(inputData(value));
        navigate("/play")
    }

    return (
        <Positioner>
            <Styled.GlobalStyle />
            <Styled.Wrapper>
                <Styled.Img><img src={imgUrl} alt='!' /></Styled.Img>
                <Styled.Title> </Styled.Title>

                <Styled.Text>1.     .</Styled.Text>
                <Styled.Text>2. 10  .</Styled.Text>
                <Styled.Text id='last'>3.  <Styled.Color> 50 ROUND</Styled.Color>.</Styled.Text>
                <Styled.InputWrapper>
                    <Styled.ReadyInput onChange={(e) => setValue(e.target.value)} placeholder=" "/>
                    <Button color="#01a8ff" onClick={loginClick}></Button>
                </Styled.InputWrapper>
            </Styled.Wrapper>
        </Positioner>
    )
};

export default Ready;