import React from 'react';
import { useSelector } from 'react-redux';
import { Positioner } from '../../components/Wrapper/styled'
import Button from '../../components/Button'
import * as Styled from './styled';

import {FacebookShareButton, FacebookIcon,TwitterShareButton, TwitterIcon} from "react-share";
import { CopyToClipboard } from "react-copy-to-clipboard";

const End = () => {
    const round = useSelector((state) => state.round.value);
    const nickname = useSelector((state) => state.nickname.value);
    const power = useSelector((state) => state.power.value);
    const currentUrl = window.location.href;

//     const comma = power.toString()
//   .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");

    return (
        <Positioner>
            <Styled.GlobalStyle />
            <Styled.Wrapper>
                <Styled.Title>?</Styled.Title>
                <Styled.Round> ROUND <Styled.Color>{round}</Styled.Color> / 50 </Styled.Round>
                <Styled.Text>  : {nickname} </Styled.Text>
                <Styled.ResultWrapper>
                    <Styled.Power>  : {power} </Styled.Power>
                    
                    <Button color="#ff2e35" onClick={() => window.location.replace("/")}></Button>
                    <Styled.ShareText></Styled.ShareText>
                    <Styled.GridContainer>
                        <FacebookShareButton url={currentUrl}>
                            <FacebookIcon size={48} round={true} borderRadius={24}></FacebookIcon>
                        </FacebookShareButton>
                        <TwitterShareButton url={currentUrl}>
                            <TwitterIcon size={48} round={true} borderRadius={24}></TwitterIcon>
                        </TwitterShareButton>
                        <Styled.Color>
                            <CopyToClipboard text={currentUrl}>
                                <Styled.URLShareButton onClick={() => alert(".")}>URL</Styled.URLShareButton>
                            </CopyToClipboard>
                        </Styled.Color>
                        {/* <button>kakao</button> */}
                    </Styled.GridContainer>
                </Styled.ResultWrapper>
            </Styled.Wrapper>
        </Positioner>
    )
};

export default End;